import * as React from 'react';
import { useHistory } from 'react-router';
// MUI Components
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';

// React-icons
import { AiOutlineLock, AiOutlineSetting } from 'react-icons/ai';
import { IoMdKeypad } from 'react-icons/io';

export default function AdminMenu(props) {

  const history = useHistory();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleNav = (path) => {
    history.push(path);
  }

  return (
    <React.Fragment>
      <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'center' }}>
        <Tooltip title="Account settings" position="bottom" arrow={true}>
          <IconButton onClick={handleClick} size="medium" sx={{ backgroundColor: 'transparent' }}>
            <Avatar sx={{ width: 35, height: 35 }} src={props.profile} />
          </IconButton>
        </Tooltip>
      </Box>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        PaperProps={{
          elevation: 0,
          sx: {
            overflow: 'visible',
            filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
            mt: 0.6,
            ml: -0.5,
            minWidth: 200,
            '& .MuiAvatar-root': {
              width: 32,
              height: 32,
              ml: -0.5,
              mr: 1,
            },
            '&:before': {
              content: '""',
              display: 'block',
              position: 'absolute',
              top: 0,
              right: 14,
              width: 10,
              height: 10,
              bgcolor: 'background.paper',
              transform: 'translateY(-50%) rotate(45deg)',
              zIndex: 0,
            },
          },
        }}
        transformOrigin={{ horizontal: 'right', vertical: 'top' }}
        anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
      >
        <MenuItem onClick={() => handleNav("/admin/profile")}>
          <ListItemIcon style={{ minWidth: '25%' }}>
            <AiOutlineSetting size={20} />
          </ListItemIcon>
          <ListItemText primary="My Settings" />
        </MenuItem>
        <MenuItem>
          <ListItemIcon style={{ minWidth: '25%' }}>
            <AiOutlineLock size={20} />
          </ListItemIcon>
          <ListItemText primary="Change Password" />
        </MenuItem>
        <MenuItem>
          <ListItemIcon style={{ minWidth: '25%' }}>
            <IoMdKeypad size={20} />
          </ListItemIcon>
          <ListItemText primary="Change Pin" />
        </MenuItem> 
      </Menu>
    </React.Fragment>
  );
}
