import {
    FaRegAddressCard,
    FaCog,
    FaFileContract,
    FaMoneyBillAlt,
    FaUsers,
    FaFileAlt
} from "react-icons/fa"

import {
    MdDashboard,
} from "react-icons/md"

import {
    GoSettings
} from "react-icons/go"

const menu = [
    {
        "label": "Dashboard",
        "route": "/manager",
        "icon": <MdDashboard size={20} />
    },
    {
        "label": "Reports",
        "route": "/manager/reports",
        "icon": <FaFileAlt size={20} />
    },
    {
        "label": "Membership",
        "route": "/manager/membership",
        "icon": <FaRegAddressCard size={20} />
    },
    {
        "label": "Loans",
        "route": "/manager/loans",
        "icon": <FaFileContract size={20} />
    },
    {
        "label": "Payments",
        "route": "/manager/payments",
        "icon": <FaMoneyBillAlt size={20} />
    },   
    {
        "label": "Manage",
        "route": "/",
        "icon": <FaCog size={20} />,
        "collapse": true,
        "collapseItems": [
            {
                "label": "Users",
                "route": "/manager/users",
                "icon": <FaUsers size={20} />
            },
            {
                "label": "System",
                "route": "/manager/system",
                "icon": <GoSettings size={20} />
            }            
        ]
    }
];

export { menu };