import * as React from 'react';
import Box from '@mui/material/Box';
import Badge from '@mui/material/Badge';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import { FaRegQuestionCircle, FaQuestionCircle } from 'react-icons/fa';
import { BiSupport } from 'react-icons/bi';
import { useHistory } from 'react-router';

export default function Help(props) {

    const history = useHistory();

    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const [iconClicked, setIconClicked] = React.useState(false);
    const [badgeCount, setBadgeCount] = React.useState(5);

    const handleOpen = (event) => {
        setAnchorEl(event.currentTarget);
        setBadgeCount(0);
        setIconClicked(true);
    };

    const handleClose = () => {
        setAnchorEl(null);
        setIconClicked(false);
    };

    const handleNav = (path) => {
        history.push(path);
        handleClose();
    }



    return (
        <React.Fragment>
            <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'center' }}>
                <Tooltip title="Help & Support" position="bottom" arrow={true}>
                    <IconButton color="inherit" style={{ backgroundColor: 'transparent' }} onClick={handleOpen}>
                        <Badge variant="dot" color="secondary" badgeContent={badgeCount} max={1} overlap="circular">
                            {!iconClicked && <FaRegQuestionCircle size={23} />}
                            {iconClicked && <FaQuestionCircle size={23} />}
                        </Badge>
                    </IconButton>
                </Tooltip>
            </Box>
            <Menu
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                onClick={handleClose}
                PaperProps={{
                    elevation: 0,
                    sx: {
                        overflow: 'visible',
                        filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                        mt: 0.6,
                        ml: -0.5,
                        minWidth: 200,
                        '& .MuiAvatar-root': {
                            width: 32,
                            height: 32,
                            ml: -0.5,
                            mr: 1,
                        },
                        '&:before': {
                            content: '""',
                            display: 'block',
                            position: 'absolute',
                            top: 0,
                            right: 14,
                            width: 10,
                            height: 10,
                            bgcolor: 'background.paper',
                            transform: 'translateY(-50%) rotate(45deg)',
                            zIndex: 0,
                        },
                    },
                }}
                transformOrigin={{ horizontal: 'right', vertical: 'top' }}
                anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
                disableScrollLock
            >
                <MenuItem onClick={() => handleNav('/faq')}>
                    <ListItemIcon>
                        <FaRegQuestionCircle size={20} />
                    </ListItemIcon> FAQ
                </MenuItem>
                <Divider />
                <MenuItem onClick={() => handleNav('/support')}>
                    <ListItemIcon>
                        <BiSupport size={20} />
                    </ListItemIcon> Support
                </MenuItem>
            </Menu>
        </React.Fragment>
    );
}
