import React from "react";
import ListItem  from "@mui/material/ListItem";
import ListItemIcon  from "@mui/material/ListItemIcon";
import ListItemText  from "@mui/material/ListItemText";
import Tooltip  from "@mui/material/Tooltip";
import { NavLink } from "react-router-dom";

import { useStyles } from "./styles";


export default function NavItem(props) {

    const classes = useStyles();

    return (
        <div>
            {props.open && (
                <ListItem
                    component={NavLink}
                    exact
                    to={props.options.route}
                    alignItems="center"
                    button
                    activeClassName={classes.active}
                >
                    <ListItemIcon className={classes.navItemIcon}>
                        {props.options.icon}
                    </ListItemIcon>
                    <ListItemText primary={props.options.label} />
                </ListItem>
            )}
            {!props.open && (
                <Tooltip arrow={true} title={props.options.label} placement="right">
                    <ListItem
                        component={NavLink}
                        exact
                        to={props.options.route}
                        alignItems="center"
                        button
                        activeClassName={classes.active}
                    >
                        <ListItemIcon className={classes.navItemIcon}>
                            {props.options.icon}
                        </ListItemIcon>
                        <ListItemText className={classes.navItemText} primary={props.options.label} />
                    </ListItem>
                </Tooltip>
            )}
        </div>
    );
}