import CompanyLogo from '../../../../../assets/logo.png';

export default function Logo(props) {
    return (
        <div>
            {props.size === "xs" &&
                <img src={CompanyLogo} alt="Company Logo" style={{ width: '2rem', height: '2rem' }} />
            }

            {props.size === "sm" &&
                <img src={CompanyLogo} alt="Company Logo" style={{ width: '2.8rem', height: '2.8rem' }} />
            }

            {props.size === "md" &&
                <img src={CompanyLogo} alt="Company Logo" style={{ width: '3.5rem', height: '3.5rem' }} />
            }

            {props.size === "lg" &&
                <img src={CompanyLogo} alt="Company Logo" style={{ width: '5rem', height: '5rem' }} />
            }

            {props.size === "xl" &&
                <img src={CompanyLogo} alt="Company Logo" style={{ width: '7rem', height: '7rem' }} />
            }
        </div>
    )
}
