import React from 'react'; 
import Box from "@mui/material/Box";

import Logo from '../Logo/Logo';
import { Typography } from '@mui/material';

export default function Brand(props) {
    return (
        <Box display="flex" justifyContent="center" alignItems="center" flexDirection="column" sx={{ width: props.width }}>
            {!props.open &&
                <Box sx={{ textAlign: "center", pt: 0.7 }}>
                    <Logo size="sm" />
                </Box>
            }
            {props.open &&
                <div style={{ width: "100%", margin: 0, padding: 0 }}>
                    <Box sx={{ textAlign: "center", pt: 2 }}>
                        <Logo size="lg" />
                    </Box>
                    <Box sx={{
                        zIndex: 100,
                        position: 'absolute',
                        left: 0,
                        right: 0,
                        top: 95,
                        border: "none",
                        backgroundColor: "#00BFA5",
                        color: "#FFF",
                        textAlign: 'center',
                        pt: 1,
                    }}>
                        <Typography variant="h6" sx={{ pt: 1, }}>Bangko sa Balay</Typography>
                        <Typography variant="body1" sx={{ pb: 2, }}>Foundation Inc.</Typography>
                    </Box>
                </div>
            }
        </Box>
    )
}