import React from 'react';
import debounce from '@mui/utils/debounce';
import { makeStyles } from '@mui/styles';
import { IoMdSearch } from 'react-icons/io'
import IconButton from '@mui/material/IconButton';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';

import { BiChevronDown } from 'react-icons/bi'

const useStyles = makeStyles((theme) => ({
    searchBox: {
        backgroundColor: "#fff",
        borderRadius: 5,
        minWidth: '100%',
    },
}));

function SearchOptions(props) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleSelect = (value) => {

        if (props.setOption) {
            props.setOption(value);
        }

        handleClose();
    }

    return (
        <div>
            <IconButton
                color="default"
                disableRipple={true}
                size="small"
                onClick={handleClick}
                sx={{ pr: 0, backgroundColor: "transparent", "&:hover": { backgroundColor: "transparent" } }}
            >
                <Typography sx={{ mr: 0.5 }}>{props.option ?? "Member"}</Typography><BiChevronDown />
            </IconButton>
            <Menu
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
            >
                <MenuItem onClick={() => handleSelect("Member")}>Member</MenuItem>
                <MenuItem onClick={() => handleSelect("Employee")}>Employee</MenuItem>
            </Menu>
        </div>
    );
}

export default function SearchBox(props) {

    const classes = useStyles();

    const handleSearch = debounce((str) => {
        if(str.length > 2){
            props.handleSearch(str);
        }
    }, 1500);

    return (
        <Autocomplete
            disableClearable
            options={[]}
            size="small"
            sx={{
                width: 400,
                mr: 2
            }}
            onInputChange={(e) => handleSearch(e.target.value)}
            renderInput={(params) => (
                <TextField
                    {...params}
                    className={classes.searchBox}
                    size="small"
                    placeholder="Search..."
                    variant="outlined"                   
                    InputProps={{
                        startAdornment: <IconButton color="primary" disableRipple={true} size="small"><IoMdSearch size={17} /></IconButton>,
                        endAdornment: SearchOptions(props)
                    }}
                    sx={{
                        mb: 1,
                        mt: 1,
                        mr: 3
                    }}
                />
            )}
        />

    )
}
