import * as React from 'react';
import Scrollbars from 'react-custom-scrollbars-2';
import { useHistory } from 'react-router';
import { NavLink } from 'react-router-dom';


// MUI Components
import Box from '@mui/material/Box';
import Badge from '@mui/material/Badge';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';

// react-icons
import { FaBell, FaBullhorn, FaCog, FaRegBell } from 'react-icons/fa';


export default function Notifications(props) {

    const history = useHistory();

    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const [iconClicked, setIconClicked] = React.useState(false);
    const [badgeCount, setBadgeCount] = React.useState(5);

    const handleOpen = (event) => {
        setAnchorEl(event.currentTarget);
        setBadgeCount(0);
        setIconClicked(true);
    };

    const handleClose = () => {
        setAnchorEl(null);
        setIconClicked(false);
    };

    const handleNav = (path) => {
        history.push(path);
        handleClose();
    }

    return (
        <React.Fragment>
            <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'center' }}>
                <Tooltip title="Account settings" position="bottom" arrow={true}>
                    <IconButton color="inherit" style={{ backgroundColor: 'transparent', marginTop: 0 }} onClick={handleOpen}>
                        <Badge anchorOrigin={{
                            vertical: 'bottom',
                            horizontal: 'right',
                        }}
                            color="secondary"
                            badgeContent={badgeCount}
                            max={10}
                            overlap="rectangular"
                        >
                            {!iconClicked && <FaRegBell size={23} />}
                            {iconClicked && <FaBell size={23} />}
                        </Badge>
                    </IconButton>
                </Tooltip>
            </Box>
            <Menu
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                onClick={handleClose}
                PaperProps={{
                    elevation: 0,
                    sx: {
                        overflow: 'visible',
                        textOverflow: 'ellipsis',
                        filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                        mt: 0.6,
                        ml: 0,
                        minWidth: 300,
                        '& .MuiAvatar-root': {
                            width: 32,
                            height: 32,
                            ml: -0.5,
                            mr: 1,
                        },
                        '&:before': {
                            content: '""',
                            display: 'block',
                            position: 'absolute',
                            top: 0,
                            right: 14,
                            width: 10,
                            height: 10,
                            bgcolor: 'background.paper',
                            transform: 'translateY(-50%) rotate(45deg)',
                            zIndex: 0,
                        },
                    },
                }}
                transformOrigin={{ horizontal: 'right', vertical: 'top' }}
                anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
                disableScrollLock
            >
                <Scrollbars style={{ minHeight: 200 }}>
                    <MenuItem onClick={() => handleNav("/announcements")}>
                        <ListItemIcon style={{ minWidth: '15%' }}>
                            <FaBullhorn size={25} />
                        </ListItemIcon>
                        <ListItemText primary="Announcement" secondary="1 hour ago" />
                    </MenuItem>
                    <MenuItem onClick={() => handleNav("/messages")}>
                        <ListItemIcon style={{ minWidth: '15%' }}>
                            <FaCog size={25} />
                        </ListItemIcon>
                        <ListItemText primary="System Message" secondary="5 Days ago" />
                    </MenuItem>
                </Scrollbars>
                <Divider />
                <MenuItem>
                    <ListItem
                        component={NavLink}
                        exact
                        to={"/notifications"}
                        alignItems="center"
                        button
                        sx={{ textAlign: "center", "&:hover": { backgroundColor: "transparent" } }}
                    >
                        <ListItemText primary={"View all notifications"} />
                    </ListItem>
                </MenuItem>
            </Menu>
        </React.Fragment>
    );
}
