import React from "react";
import Collapse from "@mui/material/Collapse";
import Box from "@mui/material/Box";
import ListItem from "@mui/material/ListItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import Tooltip from "@mui/material/Tooltip";
import { MdChevronRight, MdExpandMore } from "react-icons/md";
import NavCollapseItem from "./NavCollapseItem";
import { useStyles } from "./styles";


export default function NavItemCollapse(props) {
    const classes = useStyles();
    const collapseRef = React.createRef();
    const [isCollapse, setIsCollapse] = React.useState(-1);
    const [toggle, setToggle] = React.useState(true);

    const handleCollapse = (option) => {
        if (toggle) {
            setIsCollapse(option)
            setToggle(!toggle)
        } else {
            if (option === isCollapse) {
                setIsCollapse(0)
            } else {
                setIsCollapse(option)
            }
            setToggle(!toggle)
        }
    }

    return (
        <div>
            {props.open && (
                <ListItem button alignItems="center" onClick={() => handleCollapse(props.index)}>
                    <ListItemIcon className={classes.navCollapseItemIcon}>
                        {props.options.icon}
                    </ListItemIcon>
                    <ListItemText className={classes.navItemText} primary={props.options.label} />
                    {props.index === isCollapse ? <MdExpandMore /> : <MdChevronRight />}
                </ListItem>
            )}
            {!props.open && (
                <div>
                    <Tooltip arrow={true} title={props.options.label} placement="right">
                        <ListItem button alignItems="center" onClick={() => handleCollapse(props.index)}>
                            <ListItemIcon className={classes.navCollapseItemIcon} >
                                {props.options.icon}
                            </ListItemIcon>
                            <ListItemText className={classes.navItemText} primary={props.options.label} />
                            {props.index === isCollapse ? <MdExpandMore /> : <MdChevronRight />}
                        </ListItem>
                    </Tooltip>
                </div>
            )}
            <Collapse in={isCollapse === props.index} timeout="auto" unmountOnExit ref={collapseRef}>
                {props.options.collapseItems.map((value, key) => {
                    return <Box ml={0} key={key}>
                        <NavCollapseItem options={value} open={props.open} history={props.history} />
                    </Box>;
                })
                }
            </Collapse>
        </div>
    );
}